.mobile-nav{
    position: fixed;
    opacity:0;
    visibility: hidden;


 
    overflow: hidden;
   -webkit-overflow-scrolling: touch;
    height: 100vh;
    width: 100%;
    z-index: 50;
    background-image: linear-gradient(purple,rgb(89, 0, 255));
    
    top:0rem;

    display: flex;
    align-items: center;
    justify-content: center;

    flex-direction: column;

    &__nav-close-button{
        position: absolute;
        top:2rem;
        right: 2rem;
        background-color: transparent;
        border: none;
        font-size: 6rem;
        color: white;

    }
    &__list-container{
        display: flex;
        flex-direction: column;
        gap: 4rem;

        font-size: 5rem;
        color: white;

        &__link{
            text-decoration: none;
            &:hover{
                cursor: pointer;
            }
            &:visited{
                color: $main-color-font-light;

            }
        }

    }

}

//functionality//

//activating mobile menue//
.activated{
    opacity: 1;
    visibility: visible;
}

.mobile-menue-visible{
    opacity: 1;
    visibility: visible;
    animation-name: square-in-bottom-left;
    animation-duration: 1s;
    
}

.mobile-menue-invisible{
    opacity: 0;
    visibility: hidden;
    animation-name: square-in-bottom-left-reverse;
    animation-duration: 1s;

}