.full-img-modal{

    position: fixed;
    
    background-image: linear-gradient(rgb(0, 0, 0),rgb(0, 0, 0));
    backdrop-filter: blur(0.3rem);


   top:0rem;
  
   height: 100dvh;
   overflow:hidden;

   width: 100%;
   z-index: 80;


   display: flex;
   align-items: center;
   justify-content: center;





   &__container{
      position: relative;
     
      height: 80vh;
      width: 90%;

     /*  display: flex;
      justify-content: center;
      align-items: center;

      background-position: center;
      background-size: contain; */
    

      &__img{
        display: block;
        width: 100%;

       height: 100%;
       object-fit: contain;
      

       
      }

      &__close-button{
        background-color: transparent;
        border: none;
        position: absolute;
        font-size: 3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        top:-5rem;
        right:0rem;

        @include respond(phone){
          font-size: 5rem;

        }

        &:hover{
          cursor: pointer;

        }
      }

      &__download-button{
        position: absolute;
        bottom: -5rem;
        font-size: 3rem;
        display: flex;
        align-items: center;
        justify-content: center;
        right: 0rem;
        color: white;
        background-color: transparent;
        border: none;
        @include respond(phone){
          font-size: 5rem;

        }
        &:hover{
          cursor: pointer;

        }

      }

      
   }
   
  
}


.save-to-fav-button{
  position: absolute;
  bottom: -5rem;
  left: 0rem;
  font-size: 3rem;
  display: flex;
        align-items: center;
        justify-content: center;
        
        color: white;
        background-color: transparent;
        border: none;
        @include respond(phone){
          font-size: 5rem;

        }
        &:hover{
          cursor: pointer;

        }

        


}
 


.hidden-full-img-modal{
    opacity: 0;
    visibility: hidden;
}