.error{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);

    height: 20rem;
    width: 50rem;
    background-color: rgba(0, 0, 0, 0.356);
    backdrop-filter: blur(1rem);
    z-index: 10;
    border-radius: 5rem;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    gap: 2rem;

    @include respond(phone){
       width: 90%;
    }

    &__text{
        color: rgb(255, 255, 255);
        font-size: 3rem;
        @include respond(phone){
            font-size: 2rem;
         }
    }

    &__button{
        height: 5rem;
        width: 15rem;
        border-radius: 2rem;
        border: none;

        &__hover{
            cursor: pointer;
            
        }

    }


}