.main{
    background-color: $color-grey-light;
    width: 100%;
    height: auto;

    display: flex;
    align-items: center;
    justify-content: center;
}

.main__results-container{
    

    position: relative;

    padding-top: 10rem;
    padding-bottom: 10rem; 

    width: 100%;
    height: auto;

    &__load-more-button{
        opacity: 0;
        visibility: hidden; 
        position: absolute;
        bottom: 0rem;
        height: 10rem;
        width: 100%;
        background-color: rgb(124, 0, 0);
        color: white;
        font-family:$main-font;
        font-size: 2rem;
        transition: all 0.3s;
        border: none;
        &:hover{
            cursor: pointer;
            background-color: rgb(255, 0, 0);
            color: white;
            transition: all 0.3s;
        }
    }
    &__go-up-button{
        position: sticky;

        display: flex;
        align-items: center;
        justify-content: center;

        bottom: 2rem;
        left: 90%;

        height: 7rem;
        width: 7rem;
        border-radius: 4rem;
        border: none;
        background-color: grey;
        opacity: .7;
        z-index: 15;
        transition: all 0.3s;

        &:hover{
            cursor: pointer;
            transform: scale(1.1);
            transition: all 0.3s;

        }

        &__icon{
            font-size: 6rem;
        }
    }

    &__spiner-container{
        position: sticky;
        
        width: 10rem;
        height: 10rem;
        
        bottom: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(50%);

        display: flex;
        justify-content: center;
        align-items: center;

        &__spiner{
            color: wheat;
            font-size: 5rem;
           
           
            z-index: 10;
            
        
            animation-name: spiner;
            animation-iteration-count: infinite;
            animation-duration: 0.8s;
            animation-timing-function: linear;
        }
    }
   

}

//functionality//

.load-more-visible{ //load more button
    opacity: 1;
    visibility: visible;
}

.img-container-hidden{
    display: none;
}


.spiner-container-hidden{
    display: none;
}

//test//

.img-container-forced{

    width: 100%;
    height: auto;

    display: grid;
    position: relative;

    align-items: center;
    justify-items: center;

    grid-template-columns: repeat(auto-fit,minmax(20rem, 1fr));

    @include respond(big-desktop){
        grid-template-columns: repeat(auto-fit,minmax(20rem, 1fr));
    }

    @include respond(tab-land){
        grid-template-columns: repeat(auto-fit,minmax(20rem, 1fr));
    }

     @include respond(phone){
        grid-template-columns: repeat(auto-fit,minmax(32rem, 1fr));
    }
    grid-gap:1rem;

    &__el{
        background-color: rgb(112, 112, 112);
       position: relative;
        width: 20rem;
        height: auto;
        overflow: hidden;
        margin: 1rem;
        transition: all 0.3s;

        &:hover{
            opacity: 1;
            cursor: pointer;
            transform: scale(1.1);
            transition: all 0.3s;
        }

        @include respond(big-desktop){
            width: 20rem;
        }
        @include respond(tab-land){
            width: 20rem;
        }
        @include respond(phone){
            width: 32rem;
        }
       
        &__img{
            width: 100%;
        
        }

        &__expand-button{
         
            color: rgb(255, 0, 0);
            border: none;
            background-color: transparent;
            position: absolute;
            top:0rem;
            left: 0rem;
            font-size: 4rem;
            display: flex;
            align-items: center;
            justify-content: center;

            animation-name: icon-blinking;
            animation-duration: 4s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
            

            &:hover{
                cursor: pointer;
            }
        }

        &__download-button{
            
          
            
            border: none;
            background-color: transparent;
            position: absolute;
            bottom:0rem;
            right: 0rem;
            font-size: 4rem;
            display: flex;
            align-items: center;
            justify-content: center;

            animation-name: icon-blinking;
            animation-duration: 4s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;

            &:hover{
                cursor: pointer;
            }

        }

        &__save-to-favorite{
            
           
            
            border: none;
            background-color: transparent;
            position: absolute;
            bottom:0rem;
            left: 0rem;
            font-size: 4rem;
            display: flex;
            align-items: center;
            justify-content: center;

            animation-name: icon-blinking;
            animation-duration: 4s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
            &:hover{
                cursor: pointer;
              
            }

        }

    }
}

//functionality




.hidden-icon{
    opacity: 0;
    visibility: hidden;
}   